@function reduce ($percent) {
  @return rgba(mix(#292929, #fff), $percent / 100); }

body {
  display: flex;
  font-size: calc(.6vw + .8em);
  margin: 0;
  min-height: 100vh; }



body {
  background: white;
  color: #333; }





@import "anchor";

html {
  background-color: #282828; }

article {
  border-top: 1px solid rgba(148, 148, 148, .08); }

article:first-of-type {
  border-top: none; }

article, body > header, body > footer {
  padding: 1em calc(22% - 4em); }

body > header {
  background-color: #282828;
  color: white; }



body > footer {
  background-color: #fafafa;
  color: gray;
  border-top: 1px solid #f0f0f0; }





header h1 {
  margin-top: .4em;
  margin-bottom: .1em; }

article header {
  text-align: center;
  margin-bottom: 1.6em; }

article footer {
  padding: 1em 0; }

table {
  width: 100%; }

nav {
  margin: 1em -0.8em; }

nav a {
  display: inline-block;
  margin: 0 .8em; }

nav, body > footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }

.archive {
  h3 {
    margin-top: 0; }
  li {
    list-style-type: none; }
  a {
    font-weight: 300; } }
